<template>
  <div class="spor-news-box box">
    <div class="row">
      <div class="col-sm-6 spor-category cat-box">
        {{ category }}
      </div>
      <div class="col-sm-5 spor-date text-right dat-box">
        <p>{{ date }}</p>
      </div>
    </div>

    <div class="spor-title tit-box">
      <h4>{{ title }}</h4>
    </div>
    <div class="spor-img img-box">
      <img :src="this.imgSrc" alt="" />
    </div>
    <div class="spor-discrptions dis-box">
      <p>{{ discrption }}</p>
    </div>
    <div class="view-btn">
      <button @click="viewDetailsWindow" :class="'ptnViewDetails' + pstNo">
        <a href="#" class="fa fa-eye"></a>
        <p>View</p>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "sportnews",
  props: ["category", "date", "title", "discrption", "imgSrc", "pstNo"],
  methods: {
    viewDetailsWindow: function () {
      document.getElementById("view-box-details").style.display = "block";
      document.getElementById("details-cat").innerHTML = this.category;
      document.getElementById("details-dat").innerHTML = this.date;
      document.getElementById("details-tit").innerHTML = this.title;
      document.getElementById("details-img").src = this.imgSrc;
      document.getElementById("details-dis").innerHTML = this.discrption;
    },
  },
};
</script>

<style lang="scss">
$orangeCol: #fec194;
$redCol: rgb(243, 44, 120);
$darkblueCol: #2b3f53;
.spor-news-box {
  width: 390px;
  height: 490px;
  padding: 15px;
  margin-top: 5px;
  margin-left: 45px;
  margin-bottom: 40px;
  float: left;
  border-radius: 10%;
  box-shadow: 0 0 2.5vh black;
  background-color: $orangeCol;
  text-align: left;
  position: relative;

  .row {
    padding: 0 5px 0 5px;
    font-weight: bold;
  }
  .spor-date {
    position: absolute;
    text-align: right;
    right: 1%;
    opacity: 0.6;
  }
  .spor-category {
    width: 70px;
    height: 25px;
    background-color: $darkblueCol;
    color: $orangeCol;
    border-radius: 35%;
    text-align: center;
    overflow: hidden;
    position: absolute;
    left: 20px;
  }
  .spor-title {
    font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS",
      sans-serif;
    font-weight: bolder;
    margin-top: 35px;
    height: 65px;
  }
  .spor-img {
    width: 360px;
    height: 220px;
    margin-bottom: 10px;
    img {
      width: 360px;
      height: 220px;
    }
  }
  .spor-discrptions {
    width: 360px;
    height: 100px;
    line-height: 1.5;
    color: #666;
    overflow-y: scroll;
    padding-right: 5px;
    padding-left: 5px;
    background-clip: #f8b382;
    scroll-behavior: smooth;
    scrollbar-width: thin;
  }

  .view-btn {
    padding-top: 8px;
    button {
      width: 80px;
      height: 30px;
      position: absolute;
      left: 40px;
      cursor: pointer;
      border: 3px solid $darkblueCol;
      border-radius: 25%;
      box-shadow: 1px 1px 1px #2b3f53;
      padding-top: 3px;
      padding-left: 2px;

      p {
        font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS";
        font-size: 13px;
        font-weight: bold;
        color: $darkblueCol;
      }

      a {
        float: left;
        margin-top: 1px;
        text-decoration: none;
        color: $darkblueCol;
      }
    }
    button:hover {
      width: 84px;
      height: 31px;
      transition: 0.4s;
      font-size: 16px;
      padding-left: 4px;
      background-color: $darkblueCol;
      border: 2px solid $redCol;
      box-shadow: 3px 3px 3px $darkblueCol;
      color: $orangeCol;
      a {
        color: $orangeCol;
        font-size: 17px;
      }
      p {
        color: $orangeCol;
        font-size: 14px;
      }
    }
  }
}
</style>
<style lang="scss">
@media only screen and (max-width: 600px) {
  .elements {
    display: none;
  }
  .box{
    margin-left: 10px;
    width: 95%;
    .img-box{
      width: 100%;
      img{
        width: 100%;
      }
    }
      .dis-box{
        width: 100%;
      }
  }
}

@media only screen and (max-width: 360px) {
  .elements {
    display: none;
  }
  .box{
    margin-left: 4px;
    height: 455px;
    .dat-box{
      font-size: 12px;
    }
    .cat-box{
    font-size: 10px;
     width: 60px;
     padding: 4px 0px 0px 0px;
     left: 4px;
    }
    .tit-box{
      height: 100px;
      h4{
        font-size: 18px;
      }
    }
    .img-box{
      height: 150px;
      img{
        height: 140px;
      }
    }

  }

}
</style>